@import '../../default_variables.scss';

.cardbook__rating {
  display: flex;
  justify-content: end;
  align-items: flex-start;
  position: relative;
  height: 50px;
  line-height: 50px;
  font-size: 1.3rem;
}

.cardbook__rating label {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  cursor: pointer;
}

.cardbook__rating label:last-child {
  position: static;
}

.cardbook__rating label:nth-child(1) {
  z-index: 5;
}

.cardbook__rating label:nth-child(2) {
  z-index: 4;
}

.cardbook__rating label:nth-child(3) {
  z-index: 3;
}

.cardbook__rating label:nth-child(4) {
  z-index: 2;
}

.cardbook__rating label:nth-child(5) {
  z-index: 1;
}

.cardbook__rating label input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.cardbook__rating label .icon {
  float: left;
  color: transparent;
}

.cardbook__rating label:last-child .icon {
  color: $icon_rating_main_background;
}

.cardbook__rating:not(:hover) label input:checked~.icon,
.cardbook__rating:hover label:hover input~.icon {
  color: $icon_rating_hover_background;
}

.cardbook__rating label input:focus:not(:checked)~.icon:last-child {
  color: $icon_rating_main_background;
  text-shadow: 0 0 5px $icon_rating_hover_background;
}